const { anime } = window;


class App {
  static start() {
    return new App();
  }

  transition(targets,step){
    const duration = 1000;
    const from  = step =="enter"? 0:1;
    const to    = step =="enter"? 1:0;
    targets.style.opacity = from;
    const opacity = to;
    const anim = anime.timeline({
    easing: 'easeInOutSine',
    duration,
    });
    anim.add({
    targets,
    opacity,
    });
    return anim.finished;
  };

  constructor() {
    Promise
      .all([
        App.domReady(),
      ])
      .then(this.init.bind(this));
  }

  static domReady() {
    return new Promise(resolve => {
      document.addEventListener('DOMContentLoaded', resolve);
    });
  }

  static showPage() {
    document.documentElement.classList.add('ready');
  }

  init() {
    // Avoid "blank page" on JS error
    try {
      barba.hooks.leave(() => {
        barba.wrapper.classList.add('is-animating');
      });
      barba.hooks.after(() => {
        barba.wrapper.classList.remove('is-animating');
      });

      barba.init({
        transitions: [
          {
            sync:true,
            enter:({ next })  => this.transition(next.container, 'enter'),
            leave:({current}) => this.transition(current.container, 'leave'),
          },
        ],
      });
    
    } catch (err) {
      console.error(err);
    }

    App.showPage();
  }
}

App.start();

$(document).ready(function(){

  $("#mobile-dropdown-button").click(function () { 
    element = document.getElementById("mobile-dropdown-button")
    if((element).classList.contains("clicked")){
      element.classList.remove("clicked")
    }else{
      element.classList.add("clicked")
      anime({
        targets:"#mobile-dropdown",
        opacity:1,
        easing:"easeOutBounce",
        duration:2000,
      });
    }
    $("#mobile-dropdown").toggle();
  });
});